body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  padding: 100px 0;
}
.form {
  padding: 30px;
}
.fieldset {
  width: 100%;
  border-radius: 10px;
  padding: 15px;
  margin: 15px 0;
}
h1 {
  text-align: center;
}
.name {
  margin-top: 20px;
  border-bottom: 1px solid gray;
}
.technologies > .form__chip {
  margin: 10px;
}
.name__title {
  text-transform: capitalize;
}
.add {
  margin-top: 10px;
  border-bottom: 1px solid gray;
  padding-bottom: 20px;
}
.add > .add__btn {
  margin-left: 20px;
}
.add >.add__language {
  flex-grow: 1;
} 
.box {
  margin-top: 40px;
}
.technologies {
  margin-bottom: 20px;
}
.technologies > .technologies__title {
  margin-bottom: 10px;
}
.languages.MuiGrid-item.MuiGrid-root {
  border-bottom: 1px solid gray;
  padding-bottom: 20px;
  margin-bottom: 20px;
}
.language__btn.MuiButton-root {
  margin-top: 20px;
}
.programming__desc {
  margin-bottom: 50px;
}